import React, { FC } from 'react'
import { BlockProps } from '@lidofinance/lido-ui'
import { FallbackWalletStyle } from './styles'

export type FallbackWalletComponent = FC<
  BlockProps & {
    error: string
  }
>

export const WalletFallback: FallbackWalletComponent = ({ error, ...rest }) => {
  if (error) {
    return <FallbackWalletStyle {...rest}>{error}</FallbackWalletStyle>
  }

  return null
}
