import React, { FC } from 'react'
import { IdenticonBadge, useBreakpoint } from '@lidofinance/lido-ui'

import { WalletCardAccountStyle } from './styles'

export type WalletCardAccountProps = { account?: string | null; onClickCb: (T: unknown) => unknown }

export const WalletCardAccount: FC<WalletCardAccountProps> = (props) => {
  const { account, onClickCb, ...rest } = props

  const isMobile = useBreakpoint('md')
  const mobileSymbols = 3
  const desktopSymbols = 6
  const symbols = isMobile ? mobileSymbols : desktopSymbols

  return (
    <WalletCardAccountStyle {...rest}>
      <IdenticonBadge address={account ?? ''} symbols={symbols} onClick={onClickCb} color="accent" />
    </WalletCardAccountStyle>
  )
}
