import React, { FC, PropsWithChildren } from 'react'
import { InlineLoader } from '@lidofinance/lido-ui'

import {
  WalletCardBalanceStyle,
  WalletCardTitleStyle,
  WalletCardValueStyle,
  WalletCardExtraStyle,
  WalletCardContentStyle,
} from './styles'

export type WalletCardBalanceComponent = FC<
  PropsWithChildren<{
    title: React.ReactNode
    value: React.ReactNode
    small?: boolean
    loading?: boolean
    extra?: React.ReactNode
  }>
>

export const WalletCardBalance: WalletCardBalanceComponent = (props) => {
  const { title, small = false, extra, loading = false, children, value, ...rest } = props

  const hasExtra = extra != null
  const hasChildren = children != null

  return (
    <WalletCardBalanceStyle {...rest}>
      <WalletCardTitleStyle>{title}</WalletCardTitleStyle>
      <WalletCardValueStyle $small={small}>{loading ? <InlineLoader /> : value}</WalletCardValueStyle>
      {hasExtra && <WalletCardExtraStyle>{loading ? <InlineLoader /> : extra}</WalletCardExtraStyle>}
      {hasChildren && <WalletCardContentStyle $hidden={loading}>{children}</WalletCardContentStyle>}
    </WalletCardBalanceStyle>
  )
}
